import React, { forwardRef, useEffect, useRef, useState } from 'react';
import { arrayOf, bool, func, instanceOf, node, number, object, oneOfType, shape, string } from 'prop-types';
import classnames from 'classnames';
import { Manager, Reference, Popper } from 'react-popper';
import shortid from 'shortid';
import addDays from "date-fns/addDays";
import addMonths from "date-fns/addMonths";
import addWeeks from "date-fns/addWeeks";
import addYears from "date-fns/addYears";
import eachDayOfInterval from 'date-fns/eachDayOfInterval';
import endOfWeek from "date-fns/endOfWeek";
import format from 'date-fns/format';
import isBefore from 'date-fns/isBefore';
import isSameDay from 'date-fns/isSameDay';
import isSameMonth from 'date-fns/isSameMonth';
import isToday from 'date-fns/isToday';
import isValid from 'date-fns/isValid';
import startOfWeek from "date-fns/startOfWeek";
import subDays from "date-fns/subDays";
import subMonths from "date-fns/subMonths";
import subWeeks from "date-fns/subWeeks";
import subYears from "date-fns/subYears";
import Input from '../input';
import InputOption from '../input-option';
import ButtonIcon from '../button-icon';
import ButtonText from '../button-text';
import Icon from '../icon';
import ScreenReader from '../screen-reader';
import {
  getDateElementById,
  getNextDateNotDisabled,
  getWeekDayNames,
  getWeeksInSelectedMonth,
  isDisabled,
  isHighlighted,
  isInHoverRange,
  isInRange
} from "./date-utilities";
import {
  getDefaultLocale,
  getLocaleObject
} from '../date-selector-locale';

const CSS_PREFIX = 'vds-date-selector';
const CSS_STATE = 'vds-state';

const KEY_CODES = {
  ARROW_DOWN: 40,
  ARROW_LEFT: 37,
  ARROW_RIGHT: 39,
  ARROW_UP: 38,
  END: 35,
  ESC: 27,
  HOME: 36,
  TAB: 9,
  PAGEUP: 33,
  PAGEDOWN: 34
};

const DateSelector = forwardRef(({
  ariaLabelCalendarButtonIcon,
  ariaLabelNextMonth,
  ariaLabelNextYear,
  ariaLabelPreviousMonth,
  ariaLabelPreviousYear,
  ariaNavigationInstructions,
  className,
  closeCalendarButton,
  containerProps,
  disabledDates,
  endDate,
  formatAriaLabelDate,
  formatCalendarTitle,
  formatDateForIdString,
  formatInputValue,
  formatWeekDay,
  highlightedDates,
  iconCalendarButton,
  iconNextMonth,
  iconNextYear,
  iconPrevMonth,
  iconPrevYear,
  id = shortid.generate(),
  locale,
  maxDate,
  minDate,
  onChange,
  onSelect,
  popperProps,
  selected,
  selectsEnd,
  selectsStart,
  startDate,
  ...remainingProps
  }, ref) => {

  ref = ref || useRef();
  locale = getLocaleObject(locale) || getLocaleObject(getDefaultLocale());
  const inputButtonRef = useRef();
  const uniqueId = useRef(id);
  const uid = uniqueId.current;
  let dialogBlurTimeout = null;

  const [showCalendar, setShowCalendar] = useState();
  const [isDayFocusActive, setIsDayFocusActive] = useState(false)
  const [showNavigationHelpMessage, setShowNavigationHelpMessage] = useState();
  const [focusedDate, setFocusedDate] = useState(
    getNextDateNotDisabled((selected || new Date()), {
    before: true,
    disabledDates,
    maxDate,
    minDate
  }));
  const [selectedDate, setSelectedDate] = useState(selected);
  const [mouseEnterDate, setMouseEnterDate] = useState();
  const [inputValue, setInputValue] = useState(
    Boolean(selected) && isValid(selected)
      ? formatInputValue
        ? format(selected, formatInputValue, { locale })
        : new Intl.DateTimeFormat((locale && locale.code) || 'en-US').format(selected)
      : ''
  );
  const weeksInMonth = getWeeksInSelectedMonth(focusedDate, { locale });
  const dayNames = getWeekDayNames(weeksInMonth[0], { locale });
  const weeksWithDaysInMonth = weeksInMonth.map(week => {
    return eachDayOfInterval({
      start: startOfWeek(week, { locale }),
      end: endOfWeek(week, { locale })
    });
  });

  useEffect(() => {
    if (typeof onSelect === 'function') {
      onSelect(selectedDate);
    }

    if (showCalendar === false) {
      setFocusedDate(selectedDate || getNextDateNotDisabled((new Date()), {
        before: true,
        disabledDates,
        maxDate,
        minDate
      }));
    }

    const handleClickOutside = e => {
      if (ref.current.contains(e.target)) {
        return;
      }
      setShowCalendar(false);
    };
    document.addEventListener('click', handleClickOutside);
    document.addEventListener('touchend', handleClickOutside);

    return () => {
      document.removeEventListener('click', handleClickOutside);
      document.removeEventListener('touchend', handleClickOutside);
    };
  }, [disabledDates, maxDate, minDate, onSelect, ref, showCalendar, selectedDate]);

  useEffect(() => {
    if (Boolean(selectsEnd) && isValid(selectedDate || focusedDate) && isValid(startDate)) {
      if (isBefore((selectedDate || focusedDate), startDate)) {
        setSelectedDate(startDate);
        setFocusedDate(startDate);
        setInputValue('');
      }
    }
  }, [focusedDate, formatInputValue, locale, selectedDate, selectsEnd, startDate]);

  useEffect(() => {
    const el = getDateElementById({
      CSS_PREFIX,
      focusedDate,
      formatDateForIdString,
      locale,
      ref,
      uid
    });
    if (el && showCalendar && isDayFocusActive) {
      setTimeout(() => el.focus());
      setIsDayFocusActive(false);
    }
  }, [isDayFocusActive, uid, formatDateForIdString, locale, showCalendar, focusedDate, ref]);

  const handleInputChange = e => {
    const value = e.target.value;
    const parseValue = new Date(value);

    setShowCalendar(false);
    setInputValue(value);

    if (isValid(parseValue)) {
      setFocusedDate(parseValue);
      setSelectedDate(parseValue);
    } else {
      setFocusedDate(focusedDate);
      setSelectedDate(null);
    }

    if (typeof onChange === 'function') {
      onChange(e);
    }
  };

  const handleShowCalendar = () => {
    setShowCalendar(!showCalendar);
    setIsDayFocusActive(!showCalendar);
  };

  const handleSelectedDate = date => {
    setFocusedDate(date);
    if (formatInputValue) {
      setInputValue(format(date, formatInputValue, { locale }));
    } else {
      setInputValue(new Intl.DateTimeFormat((locale && locale.code) || 'en-US').format(date))
    }
    setSelectedDate(date);
    setShowCalendar(false);
    inputButtonRef.current.focus();
  };

  const gotoPreviousYear = () => {
    setFocusedDate(getNextDateNotDisabled(subYears(focusedDate, 1), {
      before: false,
      disabledDates,
      maxDate,
      minDate
    }));
  };

  const gotoNextYear = () => {
    setFocusedDate(getNextDateNotDisabled(addYears(focusedDate, 1), {
      before: true,
      disabledDates,
      maxDate,
      minDate
    }));
  };

  const gotoPreviousMonth= () => {
    setFocusedDate(getNextDateNotDisabled(subMonths(focusedDate, 1), {
      before: false,
      disabledDates,
      maxDate,
      minDate
    }));
  };

  const gotoNextMonth= () => {
    setFocusedDate(getNextDateNotDisabled(addMonths(focusedDate, 1), {
      before: true,
      disabledDates,
      maxDate,
      minDate
    }));
  };

  const handleDialogKeyDown = e => {
    const code = e.charCode || e.keyCode;
    const focusableElements = ref.current.querySelectorAll(`.${CSS_PREFIX}-calendar button:not([disabled]):not(.vds-state--disabled)`);
    const firstFocusableElement = focusableElements[0];
    const lastFocusableElement = focusableElements[focusableElements.length - 1];
    if ([KEY_CODES.ESC].includes(code)) {
      setShowCalendar(false);
      inputButtonRef.current.focus();
    }

    if ([KEY_CODES.TAB].includes(code)) {
      if (firstFocusableElement === e.target && e.shiftKey) {
        e.preventDefault();
        lastFocusableElement.focus();
      }

      if (lastFocusableElement === e.target && !e.shiftKey) {
        e.preventDefault();
        firstFocusableElement.focus();
      }
    }
  };

  const handleClickCloseButton = e => {
    setShowCalendar(false);
    inputButtonRef.current.focus();
  };

  const handleDialogFocus = e => {
    setShowNavigationHelpMessage(e.target.classList.contains(`${CSS_PREFIX}-day`));
    if (dialogBlurTimeout) {
      clearTimeout(dialogBlurTimeout);
    }
  };

  const handleDialogBlur = e => dialogBlurTimeout = setTimeout(() => {
    setShowNavigationHelpMessage(false);
    setShowCalendar(false);
  }, 200);

  const handleDialogClick = e => {
    if (e.target.tagName.toLowerCase() !== 'button' && dialogBlurTimeout) {
      clearTimeout(dialogBlurTimeout);
      const el = getDateElementById({
        CSS_PREFIX,
        focusedDate,
        formatDateForIdString,
        locale,
        ref,
        uid
      });
      if (el && showCalendar) {
        setTimeout(() => el.focus());
      }
    }
  };

  const handleDayMouseEnter = date => setMouseEnterDate(date);

  const handleDayMouseLeave = () => setMouseEnterDate(null);

  const handleNavigation = (e, day, locale) => {
    const code = e.charCode || e.keyCode;
    let nextDate = day;
    let before = true;

    if (!Object.keys(KEY_CODES).map(k => KEY_CODES[k]).includes(code) || [KEY_CODES.TAB].includes(code)) {
      return;
    }

    e.preventDefault();

    switch (e.keyCode) {
      case KEY_CODES.ARROW_DOWN:
        nextDate = addWeeks(day, 1);
        break;

      case KEY_CODES.ARROW_LEFT:
        before = false;
        nextDate = subDays(day, 1);
        break;

      case KEY_CODES.ARROW_RIGHT:
        nextDate = addDays(day, 1);
        break;

      case KEY_CODES.ARROW_UP:
        before = false;
        nextDate = subWeeks(day, 1);
        break;

      case KEY_CODES.END:
        before = false;
        nextDate = endOfWeek(day, { locale });
        break;

      case KEY_CODES.HOME:
        nextDate = startOfWeek(day, { locale });
        break;

      case KEY_CODES.PAGEDOWN:
        if (e.shiftKey) {
          nextDate = addYears(day, 1);
        } else {
          nextDate = addMonths(day, 1);
        }
        break;

      case KEY_CODES.PAGEUP:
        before = false;
        if (e.shiftKey) {
          nextDate = subYears(day, 1);
        } else {
          nextDate = subMonths(day, 1);
        }
        break;

      default:
        break;
    }

    nextDate = getNextDateNotDisabled(nextDate, { before, disabledDates, maxDate, minDate });

    if (Boolean(selectsStart) || Boolean(selectsEnd)) {
      setMouseEnterDate(nextDate);
    }
    setIsDayFocusActive(true);
    setFocusedDate(nextDate);
  };

  return (
    <div
      className={classnames(CSS_PREFIX, className)}
      id={`${CSS_PREFIX}-${uid}`}
      ref={ref}
      {...containerProps}
    >
      {
        <Manager>
          <Reference>
            {({ ref }) => (
              <Input
                containerProps={{ ref }}
                id={`input-${uid}`}
                onChange={handleInputChange}
                value={inputValue}
                {...remainingProps}
              >
                <InputOption>
                  <ButtonIcon
                    aria-label={ariaLabelCalendarButtonIcon(isValid(new Date(selectedDate))
                      ? formatInputValue ? format(selectedDate, formatInputValue, { locale }) : new Intl.DateTimeFormat((locale && locale.code) || 'en-US').format(selectedDate)
                      : undefined)}
                    iconType="light-tiny"
                    id={`input-button-${uid}`}
                    onClick={handleShowCalendar}
                    ref={inputButtonRef}
                  >
                    {iconCalendarButton}
                  </ButtonIcon>
                </InputOption>
              </Input>
            )}
          </Reference>
          {
            showCalendar
              ? <Popper {...popperProps}>
              {({ ref, style, placement }) => (
                // eslint-disable-next-line jsx-a11y/no-noninteractive-element-interactions
                <div
                  aria-labelledby={`${CSS_PREFIX}-header-title-${uid}`}
                  className={classnames(`${CSS_PREFIX}-calendar`)}
                  data-placement={placement}
                  onBlur={handleDialogBlur}
                  onClick={handleDialogClick}
                  onFocus={handleDialogFocus}
                  onKeyDown={handleDialogKeyDown}
                  ref={ref}
                  role="dialog"
                >
                  <div>
                    <div className={classnames(`${CSS_PREFIX}-header`)}>
                      <ButtonIcon
                        ariaLabel={ariaLabelPreviousYear}
                        iconType="light-tiny"
                        id={`${CSS_PREFIX}-header-previous-year-${uid}`}
                        onClick={gotoPreviousYear}
                      >
                        {iconPrevYear}
                      </ButtonIcon>
                      <ButtonIcon
                        ariaLabel={ariaLabelPreviousMonth}
                        iconType="light-tiny"
                        id={`${CSS_PREFIX}-header-previous-month-${uid}`}
                        onClick={gotoPreviousMonth}
                      >
                        {iconPrevMonth}
                      </ButtonIcon>
                      <div
                        aria-live="polite"
                        className={classnames(`${CSS_PREFIX}-header-title`)}
                        id={`${CSS_PREFIX}-header-title-${uid}`}
                      >
                        {format(focusedDate, formatCalendarTitle, { locale })}
                      </div>
                      <ButtonIcon
                        ariaLabel={ariaLabelNextMonth}
                        iconType="light-tiny"
                        id={`${CSS_PREFIX}-header-next-month-${uid}`}
                        onClick={gotoNextMonth}
                      >
                        {iconNextMonth}
                      </ButtonIcon>
                      <ButtonIcon
                        ariaLabel={ariaLabelNextYear}
                        iconType="light-tiny"
                        id={`${CSS_PREFIX}-header-next-year-${uid}`}
                        onClick={gotoNextYear}
                      >
                        {iconNextYear}
                      </ButtonIcon>
                      <ScreenReader aria-live="polite">{showNavigationHelpMessage ? ariaNavigationInstructions : ''}</ScreenReader>
                    </div>
                    <table
                      aria-labelledby={`${CSS_PREFIX}-header-title-${uid}`}
                      className={classnames(`${CSS_PREFIX}-calendar-grid`)}
                      role="grid"
                    >
                      <thead>
                        <tr>
                          {
                            dayNames.map((day, k) => (
                              <th
                                abbr={day.ariaAbbr}
                                className={classnames(`${CSS_PREFIX}-day-name`)}
                                key={`${CSS_PREFIX}-th-${uid}-${k}`}
                                scope="col"
                              >
                                {day.name}
                              </th>
                            ))
                          }
                        </tr>
                      </thead>
                      <tbody>
                        {
                          weeksWithDaysInMonth.map((week, k) => (
                            <tr key={`${CSS_PREFIX}-tbody-tr-${uid}-${k}`}>
                              {
                                week.map((day, i) => (
                                  <td key={`${CSS_PREFIX}-tbody-tr-td-${uid}-${k}-${i}`}>
                                    <ButtonIcon
                                      aria-label={format(day, formatAriaLabelDate, { locale })}
                                      className={classnames(`${CSS_PREFIX}-day`, {
                                        [`${CSS_PREFIX}-today`]: isToday(day),
                                        [`${CSS_PREFIX}-next-prev-month`]: !isSameMonth(day, focusedDate),
                                        [`${CSS_PREFIX}-in-range`]: isInRange(day, { startDate, endDate }),
                                        [`${CSS_PREFIX}-start-of-range`]: Boolean(selectsStart || selectsEnd) && isSameDay(day, startDate),
                                        [`${CSS_PREFIX}-end-of-range`]: Boolean(selectsStart || selectsEnd) && isSameDay(day, endDate),
                                        [`${CSS_PREFIX}-mouse-enter-range`]: isInHoverRange(day, {
                                          startDate,
                                          endDate,
                                          mouseEnterDate,
                                          selectsEnd,
                                          selectsStart
                                        }),
                                        [`${CSS_STATE}-focused`]: isSameDay(day, focusedDate),
                                        [`${CSS_STATE}-selected`]: isSameDay(day, selectedDate),
                                        [`${CSS_STATE}-disabled`]: isDisabled(day, disabledDates, maxDate, minDate),
                                        [`${CSS_STATE}-highlighted`]: isHighlighted(day, highlightedDates)
                                      })}
                                      disabled={isDisabled(day, disabledDates, maxDate, minDate)}
                                      iconType="light-tiny"
                                      id={`${CSS_PREFIX}-day-${format(day, formatDateForIdString, { locale })}-${uid}`}
                                      onClick={() => handleSelectedDate(day)}
                                      onMouseEnter={() => handleDayMouseEnter(day)}
                                      onMouseLeave={handleDayMouseLeave}
                                      onKeyDown={e => handleNavigation(e, day, locale)}
                                      tabIndex={isSameDay(day, focusedDate) ? 0 : -1}
                                    >
                                      {format(day, 'd', { locale })}
                                    </ButtonIcon>
                                  </td>
                                ))
                              }
                            </tr>
                          ))
                        }
                      </tbody>
                    </table>
                    <div className={`${CSS_PREFIX}-footer`}>
                      {closeCalendarButton(handleClickCloseButton)}
                    </div>
                  </div>
                </div>
              )}
            </Popper>
              : undefined
          }
        </Manager>
      }
    </div>
  );
});

DateSelector.propTypes = {
  /**
   * Calendar Button Icon Aria Label
   */
  ariaLabelCalendarButtonIcon: func,

  /**
   * Next Month Aria Label
   */
  ariaLabelNextMonth: string,

  /**
   * Next Year Aria Label
   */
  ariaLabelNextYear: string,

  /**
   * Previous Month Aria Label
   */
  ariaLabelPreviousMonth: string,

  /**
   * Previous Year Aria Label
   */
  ariaLabelPreviousYear: string,

  /**
   * Instructions for keyboard navigation
   */
  ariaNavigationInstructions: string,

  /**
   * @ignore
   */
  className: string,

  /**
   * Close calendar Button
   */
  closeCalendarButton: func,

  /**
   * Container props
   */
  containerProps: object,

  /**
   * Disabled dates
   */
  disabledDates: oneOfType([
    arrayOf(instanceOf(Date)),
    shape({
      end: instanceOf(Date).isRequired,
      start: instanceOf(Date).isRequired
    })
  ]),

  /**
   * End date for date range
   */
  endDate: instanceOf(Date),

  /**
   * Format for date aria label
   */
  formatAriaLabelDate: string,

  /**
   * Format for calendar title
   */
  formatCalendarTitle: string,

  /**
   * Format for date id string
   */
  formatDateForIdString: string,

  /**
   * Format for input value
   */
  formatInputValue: string,

  /**
   * Format for weekday
   */
  formatWeekDay: string,

  /**
   * Highlighted dates
   */
  highlightedDates: oneOfType([
    arrayOf(instanceOf(Date)),
    shape({
      end: instanceOf(Date).isRequired,
      start: instanceOf(Date).isRequired
    })
  ]),

  /**
   * Calendar Button icon
   */
  iconCalendarButton: node,

  /**
   * Next Month icon
   */
  iconNextMonth: node,

  /**
   * Next Year icon
   */
  iconNextYear: node,

  /**
   * Prev Month icon
   */
  iconPrevMonth: node,

  /**
   * Prev Year icon
   */
  iconPrevYear: node,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Locale
   */
  locale: oneOfType([
    string,
    shape({ locale: object })
  ]),

  /**
   * Maximum date
   */
  maxDate: instanceOf(Date),

  /**
   * Minimum date
   */
  minDate: instanceOf(Date),

  /**
   * On Change of input
   */
  onChange: func,

  /**
   * On Select of date
   */
  onSelect: func,

  /**
   * React popper props
   */
  popperProps: object,

  /**
   * Selected date
   */
  selected: instanceOf(Date),

  /**
   * Range end input
   */
  selectsEnd: bool,

  /**
   * Range start input
   */
  selectsStart: bool,

  /**
   * Start date for date range
   */
  startDate: instanceOf(Date)
};

DateSelector.displayName = 'DateSelector';

DateSelector.defaultProps = {
  ariaLabelCalendarButtonIcon: date => (
    isValid(new Date(date))
      ? `Choose Date, selected date is ${date}`
      : 'Choose Date'
  ),
  ariaLabelNextMonth: 'Next Month',
  ariaLabelNextYear: 'Next Year',
  ariaLabelPreviousMonth: 'Previous Month',
  ariaLabelPreviousYear: 'Previous Year',
  ariaNavigationInstructions: 'Cursor keys can navigate dates',
  // eslint-disable-next-line react/display-name
  closeCalendarButton: handleClickCloseButton => (
    <ButtonText
      className={classnames(`${CSS_PREFIX}-button-cancel`)}
      colorScheme="tertiary"
      onClick={handleClickCloseButton}
    >
      Close
    </ButtonText>
  ),
  iconCalendarButton: <Icon name="calendar" resolution="tiny" />,
  iconNextMonth: <Icon name="arrow-next" resolution="tiny" />,
  iconNextYear: <Icon name="fast-forward" resolution="tiny" />,
  iconPrevMonth: <Icon name="arrow-previous" resolution="tiny" />,
  iconPrevYear: <Icon name="rewind" resolution="tiny" />,
  formatAriaLabelDate: 'EEEE LLLL d, yyyy',
  formatCalendarTitle: 'LLLL yyyy',
  formatDateForIdString: 'MM-dd-yyyy',
  popperProps: {
    placement: 'bottom-start'
  }
};

export default DateSelector;
