const DateSelectorExample = () => {
  const [startDate, setStartDate] = useState();
  const [endDate, setEndDate] = useState();

  return (
    <Grid>
      <Row>
        <Col>
          <Row>
            <Col>
              <Typography tag="h2">Default Date Selector</Typography>
              <Typography>
                VDS Date-picker uses all props available to the <strong>{`<Input />`}</strong> component in
                addition to the props table below. Use this for ref and other validation needs for the input.
              </Typography>
              <Typography>
                VDS Date-picker also uses <a href="https://date-fns.org/">Date-fns</a> (think lodash utilities for Dates)
                to support locales usage and to run other date manipulation functions.
              </Typography>
              <Typography>
                <a href="https://popper.js.org/">Popper.js</a> is used for helping properly position the calendar dialog. Use popperProps to modify functionality.
              </Typography>
            </Col>
          </Row>
          <Row>
            <Col>
              <DateSelector
                label="Date (mm/dd/yyyy)"
                name="date-picker"
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <Typography tag="h3">Native Mobile Date Selector(View in iOS, Android, or Windows Phone)</Typography>
              <Typography>
                Use native date pickers for mobile operating systems when appropriate
              </Typography>
            </Col>
          </Row>
          <Row>
            <Col>
              <Input
                label="View in iOS, Android, etc"
                name="mobile-date-selector"
                type="date"
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <Typography tag="h3">Date picker with disabled dates</Typography>
              <Typography>
                Disabled March 15 and March 20 in the example below
              </Typography>
            </Col>
          </Row>
          <Row>
            <Col>
              <DateSelector
                disabledDates={[new Date('03/15/2020'), new Date('03/20/2020')]}
                label="Date (mm/dd/yyyy)"
                name="date-picker-disabled-dates"
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <Typography tag="h3">Locale (Spanish) Example</Typography>
              <Typography>
                Below is an example that imports the Spanish locale from date-fns
              </Typography>
              <Typography>
                <code>
                  {`import es from 'date-fns/locale/es';`}
                </code><br />
                <code>
                  {`<DateSelector ... locale={es} />`}
                </code><br />
              </Typography>
              <DateSelector
                ariaLabelCalendarButtonIcon={(date, formatAriaLabelDate, locale) => (
                  isValid(new Date(date))
                    ? `Elige fecha, la fecha seleccionada es ${date}`
                    : 'Elige fecha'
                )}
                ariaLabelNextMonth="prximo mes"
                ariaLabelNextYear="el prximo ao"
                ariaLabelPreviousMonth="mes anterior"
                ariaLabelPreviousYear="ao anterior"
                containerProps={{ lang: 'es' }}
                label={`La Fecha (${es.formatLong.date({ width: 'short'}).toLowerCase()})`}
                locale={es}
                name="date-picker-locale-es"
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <Typography tag="h3">Locale (Japanese &amp; Korean) Examples</Typography>
              <Typography>
                Below is an example that uses the locale string &apos;ja&apos; after importing(from date-fns) and
                registering the locale using registerLocale
              </Typography>
              <Typography>
                <code>
                  {`import { registerLocale, setDefaultLocale } from '../../date-picker-locale';`}
                </code><br />
                <code>
                  {`import ja from 'date-fns/locale/ja';`}
                </code><br />
                <code>
                  {`import ko from 'date-fns/locale/ko';`}
                </code><br />
                <code>
                  {`registerLocale('ja', ja);`}
                </code><br />
                <code>
                  {`registerLocale('ko', ko);`}
                </code><br /><br />
                <code>
                  {`GLOBAL - To set a default locale for all instances of Datepicker use the setDefaultLocale e.g.`}
                </code><br />
                <code>
                  {`setDefaultLocale('es');`}
                </code><br />
              </Typography>
            </Col>
          </Row>
          <Row>
            <Col>
              <Typography>
                <code>
                  {`<DateSelector ... locale="ja" />`}
                </code><br />
              </Typography>
              <DateSelector
                ariaLabelCalendarButtonIcon={(date, formatAriaLabelDate, locale) => (
                  isValid(new Date(date))
                    ? ` ${date}`
                    : ''
                )}
                ariaLabelNextMonth=""
                ariaLabelNextYear=""
                ariaLabelPreviousMonth=""
                ariaLabelPreviousYear=""
                containerProps={{ lang: 'ja' }}
                label={` (${ja.formatLong.date({ width: 'short'}).toLowerCase()})`}
                locale="ja"
                name="date-picker-locale-ja"
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <Typography>
                <code>
                  {`<DateSelector ... locale="ko" />`}
                </code><br />
              </Typography>
              <DateSelector
                ariaLabelCalendarButtonIcon={(date, formatAriaLabelDate, locale) => (
                  isValid(new Date(date))
                    ? ` .   ${date}`
                    : ' '
                )}
                ariaLabelNextMonth=" "
                ariaLabelNextYear=""
                ariaLabelPreviousMonth=""
                ariaLabelPreviousYear=""
                containerProps={{ lang: 'ko' }}
                label={`  (${ko.formatLong.date({ width: 'short'}).toLowerCase()})`}
                locale="ko"
                name="date-picker-locale-ko"
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <Typography tag="h2">Range Selector</Typography>
              <Typography>
                Below is an example that uses 2 date-pickers as a date range picker.
              </Typography>
            </Col>
          </Row>
          <Row>
            <Col>
              <DateSelector
                ariaLabelCalendarButtonIcon={(date, formatAriaLabelDate, locale) => (
                  isValid(new Date(date))
                    ? `Choose Start Date, selected start date is ${date}`
                    : 'Choose Start Date'
                )}
                endDate={endDate}
                label="Start Date (mm/dd/yyyy)"
                name="date-picker-before"
                onSelect={date => {
                  setStartDate(date);
                }}
                selectsStart
                startDate={startDate}
              />
              <DateSelector
                ariaLabelCalendarButtonIcon={(date, formatAriaLabelDate, locale) => (
                  isValid(new Date(date))
                    ? `Choose End Date, selected end date is ${date}`
                    : 'Choose End Date'
                )}
                endDate={endDate}
                label="End Date (mm/dd/yyyy)"
                name="date-picker-after"
                minDate={startDate}
                onSelect={date => {
                  setEndDate(date);
                }}
                selectsEnd
                startDate={startDate}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    </Grid>
  );
}
export default DateSelectorExample;
