export const registerLocale = (localeName, localeData) => {
  const scope = typeof window !== "undefined" ? window : global;

  if (!scope.__localeData__) {
    scope.__localeData__ = {};
  }
  scope.__localeData__[localeName] = localeData;
};

export const setDefaultLocale = (localeName) => {
  const scope = typeof window !== "undefined" ? window : global;

  scope.__localeId__ = localeName;
};

export const getDefaultLocale = () => {
  const scope = typeof window !== "undefined" ? window : global;

  return scope.__localeId__;
};

export const getLocaleObject = (localeSpec) => {
  if (typeof localeSpec === "string") {
    // Treat it as a locale name registered by registerLocale
    const scope = typeof window !== "undefined" ? window : global;
    const localeData = scope.__localeData__ ? scope.__localeData__[localeSpec] : null
    if (localeSpec && !localeData) {
      console.warn(
        `A locale object was not found for the provided string ["${localeSpec}"].`
      );
    }
    return localeData;
  } else {
    // Treat it as a raw date-fns locale object
    return localeSpec;
  }
};
