import addDays from 'date-fns/addDays';
import format from 'date-fns/format';
import eachWeekOfInterval from 'date-fns/eachWeekOfInterval';
import startOfMonth from 'date-fns/startOfMonth';
import endOfMonth from 'date-fns/endOfMonth';
import isAfter from 'date-fns/isAfter';
import isBefore from 'date-fns/isBefore';
import isSameDay from 'date-fns/isSameDay';
import isValid from 'date-fns/isValid';
import isWithinInterval from 'date-fns/isWithinInterval';
import subDays from "date-fns/subDays";

export const getDateElementById = ({
  CSS_PREFIX,
  focusedDate,
  formatDateForIdString,
  locale,
  ref,
  uid
}) => {
  const dateId = `#${CSS_PREFIX}-day-${format(focusedDate, formatDateForIdString, { locale })}-${uid}`;
  return ref && ref.current ? ref.current.querySelector(dateId) : null;
};

export const getDayNames = (date, locale) => {
  return [0, 1, 2, 3, 4, 5, 6].map(offset => {
    const day = addDays(date, offset);
    return {
      ariaAbbr: format(day, 'EEEE', locale),
      name: format(day, 'EEEEEE', locale)
    }
  });
};

export const getWeekDayNames = (date, locale) => getDayNames(date, locale);

export const getWeeksInSelectedMonth = (date, locale) => eachWeekOfInterval({
  start: startOfMonth(date),
  end: endOfMonth(date)
}, locale);

export const getNextDateNotDisabled = (date, { before, disabledDates, maxDate, minDate }) => {
  let isDisabledDate = isDisabled(date, disabledDates, maxDate, minDate);
  const nextDate = addDays(date, 1);
  const prevDate = subDays(date, 1);
  const hasValidMinDate = Boolean(minDate) && isValid(minDate);
  const hasValidMaxDate = Boolean(maxDate) && isValid(maxDate);
  const isLessThanMinDate = hasValidMinDate && isBefore(date, minDate);
  const isGreaterThanMaxDate = hasValidMaxDate && isAfter(date, maxDate);

  if (isDisabledDate && isLessThanMinDate) {
    return getNextDateNotDisabled(nextDate, {
      before: true,
      disabledDates,
      maxDate,
      minDate
    });
  }

  if (isDisabledDate && isGreaterThanMaxDate) {
    return getNextDateNotDisabled(prevDate, {
      before: false,
      disabledDates,
      maxDate,
      minDate
    });
  }

  if (isDisabledDate) {
    return getNextDateNotDisabled(before ? nextDate: prevDate, {
      before,
      disabledDates,
      maxDate,
      minDate
    });
  }
  return date;
};

export const isDisabled = (date, disabled, maxDate, minDate) => {
  const hasValidMinDate = Boolean(minDate) && isValid(minDate);
  const hasValidMaxDate = Boolean(maxDate) && isValid(maxDate);
  const isLessThanMinDate = hasValidMinDate && isBefore(date, minDate);
  const isGreaterThanMaxDate = hasValidMaxDate && isAfter(date, maxDate);
  let isDisabledDate = false;

  if (Array.isArray(disabled)) {
    isDisabledDate = disabled.findIndex(d => isSameDay(date, d)) > -1;
  }

  if (typeof disabled === "object" && !Array.isArray(disabled) && disabled !== null) {
    isDisabledDate = isWithinInterval(date, {...disabled });
  }
  return isLessThanMinDate || isGreaterThanMaxDate || isDisabledDate;
};

export const isHighlighted = (date, highlightedDates) => {
  if (Array.isArray(highlightedDates)) {
    return highlightedDates.findIndex(d => isSameDay(date, d)) > -1;
  }

  if (typeof highlightedDates === "object" && !Array.isArray(highlightedDates) && highlightedDates !== null) {
    return isWithinInterval(date, {...highlightedDates });
  }
};

export const isInHoverRange = (date, { startDate, endDate, mouseEnterDate, selectsEnd, selectsStart }) => {
  startDate = startDate || endDate || new Date();
  endDate = endDate || startDate || new Date();

  if (!isValid(startDate) || !isValid(endDate) || !isValid(mouseEnterDate)) {
    return;
  }

  if (Boolean(selectsStart) && isBefore(mouseEnterDate, startDate)) {
    return isWithinInterval(date, { start: mouseEnterDate, end: startDate });
  }

  if (Boolean(selectsEnd) && isAfter(mouseEnterDate, endDate)) {
    return isWithinInterval(date, { start: endDate, end: mouseEnterDate });
  }
};

export const isInRange = (date, { startDate, endDate }) => {
  if (!isValid(startDate) || !isValid(endDate)) {
    return;
  }
  if (isBefore(startDate, endDate)) {
    return isWithinInterval(date, { start: startDate, end: endDate });
  }
};

